  from {border: 1px solid $winter-grey;}
  to {border: 1px solid $hyper-violet;}
}

@keyframes borderTransitionOut {
  from {border: 1px solid $hyper-violet;}
  to {border: 1px solid $winter-grey;}
}

.Menu {
  border-right: 1px solid $winter-grey;
  display:flex;
  flex-direction:column;
  justify-content: center;
  align-content: center;
  grid-row-start:2;
  grid-column: 1 / 2;
  text-align: center;
  background-color:$cool-grey;
  padding: 10px 0px;
  margin:0px;
  padding-bottom:30px;
  width:375px;


  li {
    list-style-type: none;
  }

  .MenuItem {
    border: 1px solid $winter-grey;
    border-radius:5px;
    padding:20px;
    margin:10px 25px 10px 25px;
    background-color:white;

    box-shadow: 1px 1px 1px $winter-grey;
    transition: box-shadow 0.5s;

    .Link {
      text-decoration: none;
      color:$hyper-violet;
    }

  }

  .MenuItem:hover {

    transition: box-shadow 0.5s;
    box-shadow: 10px 10px 10px $shadow-grey;

  }

  @keyframes spin {
    100% {transform: rotate(1turn); }
  }

  .loveLine {
    display:flex;
    flex-direction: row;
    justify-content: center;
    color: $dark-grey;
    font-weight:500;
    white-space: nowrap;

    p {
      margin-right: 5px;
    }

    a {
      text-decoration: none;
      white-space: nowrap;
    }

    .heart {
      animation: spin 1s linear infinite;
      animation-play-state: paused;
    }

    .heart:hover {
      animation-play-state: running;
    }

  }

}
}
